  font-family: 'Montserrat';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-weight: 400;
  font-style: normal;
}

:root {
  --background-color: #0c79a4;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
}

section {
  padding: 1rem;
}

h2 {
  padding: 1rem;
  text-align: center;
  font-size: 2rem;
}

p {
  font-size: 1rem;
  line-height: 1.6rem;
}

.box-shadow {
  box-shadow: 2px 2px 8px black;
}

.about-me {
  padding-top: 2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.style-wrapper {
  width: 100vw;
  height: 300%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: var(--background-color);
  transform: skewY(-30deg) translateY(-90%);
}

.description-container {
  max-width: 1000px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.image-container {
  max-width: 300px;
  position: relative;
}

.image-container img {
  width: 100%;
  object-fit: cover;
  display: block;
  filter: brightness(90%);
}

.image-container h1 {
  padding: .5rem;
  position: absolute;
  bottom: 0;
  left: 0;
  color: white;
  font-size: 3rem;
  text-shadow: 4px 4px 20px black;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.icons-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

a .icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  width: 24px;
  height: auto;
}

.my-battles {
  max-width: 1000px;
  margin: 0 auto;
}

.battles-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1.5rem;
}

.battle-image{
  width: 100%;
  object-fit: cover;
}

.battle-description {
  padding: .5rem;
  display: flex;
  flex-direction: column;
  gap: .75rem;
}

.battle-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.battle-title-container .icons-container {
  gap: .1rem;
}

.battle-description p {
  font-size: .8rem;
  line-height: normal;
}

.contact {
  padding: 0;
  background-color: var(--background-color);
}

.contact-container {
  max-width: 1000px;
  margin: 0 auto;
}

.contact-info {
  padding: 2rem;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
}

.contact-info h2 {
  padding: 0;
  margin-bottom: 1rem;
}

.contact-phone,
.contact-email {
  display: flex;
  gap: .25rem;
}

.photo-container {
  max-width: 500px;
  height: auto;
}

.contact-image {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

@media screen and (min-width: 500px) {
  .style-wrapper {
    transform: skewY(-15deg) translateY(-85%);
  }

  .description-container {
    display: block;
  }

  .image-container {
    float: left;
    max-width: 250px;
    max-height: 384px;
    margin: 0 14px 10px 0;
  }

  .image-container img {
    width: 250;
    height: 384px;
  }

  .image-container h1 {
    padding: 0;
    top: -8%;
    left: 28%;
    bottom: unset;
    white-space: nowrap;
  }

  .content {
    max-width: 900px;
    display: block;
    padding: 1rem;
    background-color: white;
    box-shadow: 2px 2px 8px black;
    margin-top: 2rem;
    margin-left: 2rem;
  }

  .content .icons-container {
      justify-content: flex-end;
      margin-top: 1rem;
  }

  .contact-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
  }

  .contact-info {
    align-self: start;
  }

  .contact-info h2 {
    text-align: left;
  }

  .photo-container {
    padding: 2rem;
  }

  .photo-container img {
    max-height: 450px;
  }

  .contact-info .icons-container {
    justify-content: flex-start ;
  }
}

@media screen and (min-width: 1000px) {
  h2 {
    text-align: left;
  }

  .image-container h1 {
    padding: .5rem;
    white-space: wrap;
    top: unset;
    left: unset;
    bottom: 0;
    left: 0;
  }

  .photo-container {
    width: 500px;
    height: 376px;
  }

  .photo-container img {
    max-width: 100%;
    max-height: 100%;
  }
